.row {
  display: -ms-flexbox;
  display: flex;
}

/* animacion de simbolos */
@-webkit-keyframes symbol-appear {
  from {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes symbol-appear {
  from {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

svg {
  -webkit-animation-name: symbol-appear;
          animation-name: symbol-appear;
  -webkit-animation-duration: 0.15s;
          animation-duration: 0.15s;
}

/* Empate */
.board.tie .symbol {
  background-color: red;
}

/* Combinaciones ganadoras */
.board.win-row0 .row0 .symbol {
  background-color: greenyellow;
}
.board.win-row1 .row1 .symbol {
  background-color: greenyellow;
}
.board.win-row2 .row2 .symbol {
  background-color: greenyellow;
}
.board.win-column0 .symbol.column0 {
  background-color: greenyellow;
}
.board.win-column1 .symbol.column1 {
  background-color: greenyellow;
}
.board.win-column2 .symbol.column2 {
  background-color: greenyellow;
}
.board.win-diagonal1 .row0 .symbol.column0 {
  background-color: greenyellow;
}
.board.win-diagonal1 .row1 .symbol.column1 {
  background-color: greenyellow;
}
.board.win-diagonal1 .row2 .symbol.column2 {
  background-color: greenyellow;
}
.board.win-diagonal2 .row0 .symbol.column2 {
  background-color: greenyellow;
}
.board.win-diagonal2 .row1 .symbol.column1 {
  background-color: greenyellow;
}
.board.win-diagonal2 .row2 .symbol.column0 {
  background-color: greenyellow;
}



